import React, { useState } from 'react';
import Prompt from './Prompt';
import './Jokes.css'; // import the CSS file
import { joke_answer } from './questions';

function Jokes() {
  const [showPrompt, setShowPrompt] = useState(0);
  const [showAnswer, setShowAnswer] = useState(false)

  const handleYesClick = () => {
    setShowPrompt(2);
  };

  const handleNoClick = () => {
    setShowPrompt(1);
  };

  const getAnswerClick = () => {
    setShowAnswer(true);
  };

  const nextQuestionClick = () => {
    setShowAnswer(false);
    setShowPrompt(showPrompt + 1);
  };

  const containerStyle = {
    backgroundImage: 'linear-gradient(45deg, #FF7F50, #FF69B4, #FFBDAE)',
    backgroundPosition: '0% 50%',
    animation: 'GradientAnimation 10s ease infinite',
    minHeight: '100vh',
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'row',
    alignItems: 'center',
  };
  
  return (
    <div style={containerStyle}>
      {showPrompt === 0 && (
        <Prompt
          question="Apakah kamu odre?"
          answer = ""
          onYesClick={handleYesClick}
          onNoClick={handleNoClick}
          showAnswer = {showAnswer}
          button="2"
          minHeight="20vh"
          className="fade-in" // add the class to the component
        />
      )}
      {showPrompt === 1 && (
        <Prompt
          question="Web ini bukan buat kamu atuh"
          answer = ""
          button="-999"
          showAnswer = {showAnswer}
          onYesClick={handleYesClick}
          onNoClick={handleNoClick}
          className="fade-in" // add the class to the component
        />
      )}
      {showPrompt === 2 && (
        <Prompt
          question="I heard today was a hard day"
          answer = "So here i made you some jokes, hope it makes you feel better :D"
          button="-1"
          showAnswer = {true}
          onYesClick={nextQuestionClick}
          onNoClick={handleNoClick}
          className="fade-in" // add the class to the component
        />
      )}
      {showPrompt === 3 && (
        <Prompt
          question="Sabar yaaa, jokesnya otw :D"
          answer = ""
          button="0"
          showAnswer = {false}
          onYesClick={nextQuestionClick}
          onNoClick={handleNoClick}
          className="fade-in" // add the class to the component
        />
      )}
    </div>
  );
}

export default Jokes;
