import React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import { makeStyles } from '@mui/styles';

const useStyles = makeStyles({
  card: {
    maxWidth: '90%',
    minWidth: '30%',
    minHeight: '20vh',
    padding: '30px 20px',
    borderRadius: 25,
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  buttonGroup: {
    display: 'flex',
    justifyContent: 'space-between',
    width: '100%',
  },
  buttonGroup2: {
    display: 'flex',
    justifyContent: 'center',
    width: '100%',
  },
  button: {
    width: '40%', // Changed from '45%' to '45%'
    borderRadius: 8,
    fontWeight: 'bold',
    padding: '15px 30px',
    boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.1)',
    height: '50px'
  },
  content: {
    display: 'flex',
    height: '30vh',
    flexDirection: 'column',
    flex: 1, // Added flex property to take up remaining space
    textAlign: 'center',
  },
  image : {
    maxHeight: '350px',
    maxWidth: '350px',
  }
});

function Prompt({ question, onYesClick, onNoClick,button, className, showAnswer, answer }) {
  const classes = useStyles();
  const justifyContent = button === "0"? "center" : "start" // Added ternary operator to set alignItems property

  return (
<Card className={`${classes.card} ${className}`} elevation={5} style={{backgroundColor: 'rgba(255, 255, 255, 0.75)', boxShadow: '0px 4px 20px rgba(0, 0, 0, 0.4)', borderRadius: '16px', backdropFilter: 'blur(5px)', border: '1px solid rgba(255,255,255,0.55)'}} >
      <CardContent className={classes.content} sx={{justifyContent: {justifyContent}}}> {/* Added className to CardContent */}
        <Typography variant="h4" component="h4" sx={{ fontWeight: 550, fontFamily: 'sans-serif' }} gutterBottom>
          {question}
        </Typography>
        {showAnswer === true && (
          <Typography variant="h6" component="h46" sx={{ fontWeight: 'normal', fontFamily: 'sans-serif', textAlign: 'left' }} gutterBottom>
            {answer}
          </Typography>
        )}
      </CardContent>
      {button === "2" && (
        <div className={classes.buttonGroup}>
        <Button
          color="error"
          variant="contained"
          className={`${classes.button}`}
          onClick={onNoClick}
          sx={{fontSize: '20px'}}
        >
          bukann
        </Button>
        <Button
          color="success"
          variant="contained"
          className={`${classes.button}`}
          onClick={onYesClick}
          sx={{fontSize: '20px'}}
        >
          iyaa
        </Button>
        </div>
      )}

      {button === "-1" && (
        <div className={classes.buttonGroup2}>
        <Button
          variant="contained"
          className={`${classes.button}`}
          onClick={onYesClick}
          sx={{fontSize: '20px'}}
        >
          menuju jokes :D
        </Button>
        </div>
      )}

      {button === "-999" && (
        // image suruh pergi
        <img src='suruh pergi.png' className={classes.image}></img>
        )}
 
    </Card>
  );
}

export default Prompt;
